.nav {

  &__item {
    width: 100px;
    transform-origin: left;
    transition: transform $time-transition ;
    cursor: pointer;

    &:hover {
      transform: scale(1.1) ;
    }

    @include onDesktop {
      width: 169px;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    list-style: none;
    margin: 0;
    padding: 0;
    gap: 24px;

    @include onDesktop {
      gap: 30px;
    }
  }

  &__link {
    color: $color-white;
    font-family: "IBM Plex Sans", sans-serif;
    font-size: 16px;
    font-weight: 500;
    line-height: 100%;
    text-decoration: none;
    text-align: left;

    @include onDesktop {
      font-size: 22px;
    }
  }
}
